<template lang="pug">
#postApp
    .blockTitle Projects
    h1.articlePageTitle {{post_data.title}}
    .articleFlexBlock
        .projectContent {{post_data.content}}
        .articlePageJumpbar(v-if="post_data.image" :style="{'background-image': 'url('+post_data.image+')'}")
    .publicationBlock(v-for="content in post_data.publications")
        .publicationContent {{content.body}}
        a.projectLink(v-if="content.pdf!=''" :href="content.pdf" target="blank") [PDF] 
        a.projectLink(v-if="content.video!=''" :href="content.video" target="blank") [video] 
        a.projectLink(v-if="content.slide!=''" :href="content.slide" target="blank") [slide]

</template>

<script>

export default {
    props: ['projects'],
    data() {
        return {
            post_data: {}
        }
    },
    created() {
        let post_id = this.$route.params.id;
        for(let i=0;i<this.projects.length;i++){
            for(let j=0;j<this.projects[i].projects.length;j++){
                if(post_id == this.projects[i].projects[j].id){
                    this.post_data = this.projects[i].projects[j];
                    break;
                }
            }
        }
        
    },
    methods: {
       
    }
}

</script>

<style lang="sass" scoped>
@import "@/style/value.sass"
.articlePageTitle
    line-height: 36px
    font-weight: 500
    font-size: 24px
    letter-spacing: 2px

.articleFlexBlock
    display: flex
    justify-content: space-around
    flex-wrap: wrap
    border-bottom: 1px #efefef solid
    padding: 40px 0
.articlePageJumpbar
    background-size: contain
    width: 40%
    background-position: center
    background-repeat: no-repeat
.projectContent
    margin: 20px 0
    padding: 0 20px
    font-weight: 300
    width: 40%
@media all and (max-width: 760px)
    .articleFlexBlock
        flex-direction: column-reverse
    .articlePageJumpbar
        width: 100%
        height: 40vw
    .projectContent
        width: 100%

.publicationBlock
    margin: 20px 0
</style>


