<template lang="pug">
#ProjectList
    .blockTitle Projects
    .projectContainer(v-for="cate in projects")
        .graySectionTitle {{cate.category}}
        router-link.projectBlock(v-for="(project,idx) in cate.projects" :key="idx" :to="'post/'+project.id")
            .projectText
                .projectTitle {{project.title}}
                .projectContent {{project.content}}
            .projectImg(:style="{'background-image': 'url('+project.image+')'}")
    
</template>

<script>
export default {
    props: ['projects'],
    components: {
    },
    data () {
        return {
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
.projectBlock
    display: flex
    flex-wrap: wrap-reverse
    justify-content: space-between
    align-items: flex-end
    transition: 0.4s
    &:hover
        opacity: 0.8
        transform: translate(5px, -5px)
.projectText
    width: 60%
    .projectTitle
        font-weight: bold
        font-size: 1.2em
        cursor: pointer
        color: #000
        letter-spacing: 2px
    .projectContent
        margin: 20px 0
        padding: 0 20px
        border-left: 3px solid $c-sec
        font-weight: 300
        color: #000
@media (min-width: 1200px)
    .projectBlock
        margin: 50px 0
@media (max-width: 1200px)
    .projectBlock
        margin: 10px 0
    .projectText
        width: 50%
@media (max-width: 960px)
    .projectText
        width: 100%
.projectImg
    width: 360px
    height: 240px
    background-size: contain
    background-position: center
    background-repeat: no-repeat
    margin: 10px 0
</style>



