<template lang="pug">
.sraffPage
    .staffContainer(v-for='pos in data')
        .chairTitle {{pos.title}}
        .staffListContainer
            .staffListContentBlock(v-for='item in pos.staff')
                .staffImg(:style="{'background-image': 'url('+item.imgSrc+')'}")
                .staffListName {{item.name}}
                .staffListTitle {{item.agency}}
                a.staffListLink(v-if='item.href!=""' :href="item.href") website
</template>

<script>
export default {
    components: {
        
    },
    data () {
        return {
            name: "Members",
            data: [
                {title: "Faculty",
                staff: [
                    {imgSrc: require('@/assets/member/chuang-wen.png'),name: 'Chuang-Wen You',agency: 'Assistant Professor',href: "#/members/cwyou/home"}
                ]},
                {title: "Post-doctoral Researcher / Research Assistant",
                staff: [
                    {imgSrc: require('@/assets/member/minwei.jpg'),name: 'Min-Wei Hung',agency: 'Research Assistant',href: "https://sites.google.com/view/min-wei-hung-mt-ur"}
                ]},
                {title: "Current Graduate Students",
                staff: [
                    {imgSrc: require('@/assets/member/hsueh-sung.jpg'),name: 'Hsueh-Sung Lu',agency: 'MS Program at ISA',href: ""},
                    {imgSrc: require('@/assets/member/chieh-jui.jpg'),name: 'Chieh-Jui Ho',agency: 'Undergraduates at IPTA',href: ""},
                    {imgSrc: require('@/assets/member/chi-ting.jpg'),name: 'Chi-Ting Hou',agency: 'Undergraduates at IPTA',href: ""},
                    {imgSrc: require('@/assets/member/avatar.png'),name: 'Pin-Chieh Chen',agency: 'Undergraduates at IPTA',href: ""}
                ]}
            ]
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"

.sraffPage
    padding-top: 50px
.TitleU
    text-align: center
    letter-spacing: 2px
    margin: 20px 0

.staffContainer
    max-width: 1080px
    width: 80%
    margin: auto
    border-bottom: 1px #efefef solid
    margin-bottom: 50px

@media (max-width: 960px)
    .staffListContentBlock
        width: 100%
    .staffListContainer
        width: 100%
        justify-content: center
    .staffContainer
        flex-flow: wrap
    .staffListContentBlock
        padding-top: 15px
        margin-top: 0 !important
    .chairTitle
        text-align: center
        
.staffListContentBlock
    margin: 30px 
    width: 200px
    margin-left: 40px
    text-align: center

.staffListContainer
    display: flex
    flex-flow: wrap

.staffImg
    height: 180px
    width: 180px
    background-size: contain
    background-repeat: no-repeat
    background-position: center
    border-radius: 50%
    margin: 20px auto

.staffListLink
    font-size: 17px
    font-weight: 300
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #d98a30
.staffListName
    font-size: 19px
    font-weight: 300
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #000

.staffListTitle
    font-size: 17px
    font-weight: 300
    margin: 20px 0 10px 0
    width: 100%
    font-family: 'Roboto', sans-serif
.chairTitle 
    width: fit-content
    padding-bottom: 10px
    border-bottom: 3px solid $c-sec
    font-size: 20px
    font-weight: 500
    letter-spacing: 2px
</style>



